import react from "react";
import "./App.css";

function Contact(props){

    return(
           <li class="contact" onClick={()=>{
             props.manipulate(props.details, props.index);
           }}>
              <div class="wrap">
                <span class="contact-status online"></span>
                <img src={props.details.image} alt="" />
                <div class="meta">
                  <p class="name">{props.details.name}</p>
                  <p class="preview"></p>
                </div>
              </div>
            </li>
    );
}
export default Contact;