import react from "react";
import Contact from "./Contact.js";
import App from "./App.js";

function Message(props){
function imgcheck(check){
    if(check){
        return "http://emilcarlsson.se/assets/mikeross.png";
    }else{
        return props.img;
    }
}
    function statuscheck(check){
        console.log(check)
        if(check){
            return "sent";
        }else{
            return "replies";
        }
    }
    return(
        <li class={
           statuscheck(props.mess.sent)
        }>
        <img src={
            imgcheck(props.mess.sent)    
        } alt="" />
        <p>
         {props.mess.msg}
        </p>
      </li>
    );

}
export default Message;