function App() {
  const[currentcontent, update]=useState({ms:contacts[0],index:0});
function contentupdate(obj,ind){
  var objupdate={ms:obj, index:ind}
update(objupdate);
}
function contactupdate(index, msginput){
     contacts[index].message.push({
        msg:msginput,
        sent:true
     })
     contentupdate(contacts[index], index);
}
  return (
    <div id="frame">
      <div id="sidepanel">
        <div id="profile">
          <div class="wrap">
            <img
              id="profile-img"
              src="http://emilcarlsson.se/assets/mikeross.png"
              class="online"
              alt=""
            />
            <p>Mike Ross</p>
          </div>
        </div>
        <div id="contacts">
          <ul>
            {contacts.map((i, j) => {
              return <Contact details={i} index={j}  manipulate={contentupdate}></Contact>;
            })}
          </ul>
        </div>
        <div id="bottom-bar">
          <button id="addcontact">
            <i class="fa fa-user-plus fa-fw" aria-hidden="true"></i>{" "}
            <span>Add contact</span>
          </button>
          <button id="settings">
            <i class="fa fa-cog fa-fw" aria-hidden="true"></i>{" "}
            <span>Settings</span>
          </button>
        </div>
      </div>
     <Content show={currentcontent} manipulate={contentupdate} input={contactupdate}></Content>
    </div>
  );
}

export default App;
