import react from "react";
import Contact from "./Contact.js";
import App from "./App.js";
import Message from "./Message.js";
function Content(props) {
    {console.log(props.show.ms)}
  return (
    <div class="content">
      <div class="contact-profile">
        <img src={props.show.ms.image} alt="" />
        <p>{props.show.ms.name}</p>
      </div>
      <div class="messages">
        <ul>
            {
                props.show.ms.message.map((i)=>{
                    console.log(i, "#");
                   return  <Message mess={i} img={props.show.ms.image} ></Message>
                })
            }
        </ul>
      </div>
      <div class="message-input">
        <div class="wrap">
          <input type="text" placeholder="Write your message..." id="msg"/>
          <i class="fa fa-paperclip attachment" aria-hidden="true"></i>
          <button class="submit" onClick={()=>{
                     props.input(props.show.index, document.getElementById("msg").value);
                     document.getElementById("msg").value=null;
          }}>
            <i class="fa fa-paper-plane" aria-hidden="true"></i>
          </button>
        </div>
      </div>
    </div>
  );
}
export default Content;
